<template>
  <div>
    <span class="institution-inner" v-if="assets.length">
      <img
        v-if="getAssetIcon(selectedAsset)"
        :src="getAssetIcon(selectedAsset)"
        alt="Wealthica"
      />

      <i v-else class="wicon" :class="iconClassName(selectedAsset.type)" />
    </span>
    <span v-if="assets.length - 1 > 0" class="institution-number"
      >{{ assets.length - 1 }}+</span
    >

    <select
      class="account-drop-down"
      ref="assetId"
      v-model="assetId"
      :class="{
        'account-drop-down-error': $v.assetId && $v.assetId.$error,
      }"
      @change="updateAssetId"
    >
      <option selected value="">Select an asset to import to.</option>
      <option v-for="asset in assets" :key="asset._id" :value="asset._id">
        {{ asset.name }}
      </option>
    </select>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ASSET_TYPES from "../utils/assets";
export default {
  inject: ["$v"],
  data() {
    return {
      assetId: "",
    };
  },
  computed: {
    ...mapGetters(["assets"]),
    selectedAsset() {
      if (this.assetId) {
        const assets = this.assets.filter((asset) => asset._id == this.assetId);
        return assets[0];
      } else {
        return this.assets[0];
      }
    },
  },
  methods: {
    updateAssetId() {
      this.$emit("updateAssetId", this.assetId);
    },
    getAssetIcon(asset) {
      if (asset.image) return asset.image;
      return null;
    },
    iconClassName(type) {
      let icon;
      Object.entries(ASSET_TYPES).forEach((entry) => {
        const [assetType] = entry;
        if (assetType === type) {
          icon = ASSET_TYPES[assetType].icon;
        } else if (
          ASSET_TYPES[assetType].sub_types &&
          ASSET_TYPES[assetType].sub_types[type]
        ) {
          icon =
            ASSET_TYPES[assetType].sub_types[type].icon ||
            ASSET_TYPES[assetType].icon;
        }
      });
      return icon;
    },
  },
};
</script>
