<template>
  <div>
    <span class="institution-inner" v-if="institutions.length">
      <img
        :src="`https://app.wealthica.com/images/institutions/${getInstitutionType}.png`"
        alt="Wealthica"
    /></span>
    <span v-if="institutions.length - 1 > 0" class="institution-number"
      >{{ institutions.length - 1 }}+</span
    >
    <select
      ref="institutionId"
      class="account-drop-down"
      v-model="institutionId"
      :class="{
        'account-drop-down-error': $v.institutionId && $v.institutionId.$error,
      }"
      @change="updateInstiutionId"
    >
      <option selected value="">Select an institution to import to.</option>
      <option
        v-for="institution in institutions"
        :key="institution.id"
        :value="institution.id"
      >
        {{ institution.name }}
      </option>
    </select>

    <select
      v-if="institutionId"
      class="account-drop-down"
      ref="investmentId"
      v-model="investmentId"
      :class="{
        'account-drop-down-error': $v.investmentId && $v.investmentId.$error,
      }"
      @change="updateInvestmentId"
    >
      <option selected value="">Select an investment to import to.</option>
      <option
        v-for="investment in investments"
        :key="investment._id"
        :value="investment._id"
      >
        {{ investment.name }}
      </option>
    </select>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  inject: ["$v"],
  data() {
    return {
      institutionId: "",
      investmentId: "",
    };
  },
  computed: {
    ...mapGetters(["institutions", "assets"]),
    investments() {
      if (this.institutionId) {
        return this.institutions.filter(
          (institution) => institution.id === this.institutionId
        )[0].investments;
      } else {
        return [];
      }
    },
    getInstitutionType() {
      if (!this.institutionId) {
        return this.institutions[0].type;
      } else {
        const institutions = this.institutions.filter(
          (institution) => institution.id == this.institutionId
        );
        return institutions[0].type;
      }
    },
  },
  methods: {
    updateInstiutionId() {
      this.$emit("updateInstiutionId", this.institutionId);
    },
    updateInvestmentId() {
      this.$emit("updateInvestmentId", this.investmentId);
    },
  },
};
</script>
